<script>
import FlowList from '~/components/flow/FlowList'
export default {
  name: 'UserBangumi',
  components: {
    FlowList
  },
  async asyncData({ store, params }) {
    await store.dispatch('flow/initData', {
      func: 'getUserFollowBangumis',
      sort: 'news',
      type: 'page',
      count: 9999,
      id: params.zone
    })
  },
  props: {
    zone: {
      type: String,
      required: true
    }
  }
}
